.section-title {
  margin: 0;

  margin-bottom: 48px;
  font-style: normal;
  font-weight: 500;
  font-size: 32px;
  line-height: 100%;
  text-align: center;
  letter-spacing: -1.5px;
  color: $main-text-color;

  @include on-tablet {
    font-size: 48px;
  }

  @include on-desktop {
    font-size: 64px;
    margin-bottom: 56px;
  }

  &--align--left {
    text-align-last: left;
  }
}
